.pricing{
    width: 100%;
    padding: 6rem 1rem;
    background-color: black;
}
.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
}
.card{
    border: 1px solid rgb(128, 123, 123);
    color: rgb(128, 123, 123);
    text-align: center;
    padding: 1rem;
    border-radius: 8px;
}
.card:hover{
    background-color: rgb(27, 25, 25);
}
.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}
.card p{
    padding: 10px 0;
}
.bar{
    border-bottom: 1px solid rgb(128, 123, 123);
    width: 10%;
    margin:1.5rem auto;
    display: block;
}
.btc{
    font-size: 4rem;
    font-weight: 400;
    padding: 1rem 0;

}
.card .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;
}
@media screen and (max-width:740px) {
    .card-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;
    }
}